
/* Swipers */

document.querySelectorAll('.servicesSwiper').forEach((el, index) => {
    new Swiper(el, {
      slidesPerView: 1.1,
      spaceBetween: 10,
      navigation: {
        nextEl: ".services__swiper-button-next",
        prevEl: ".services__swiper-button-prev",
      },
      pagination: {
        el: ".services__swiper-pagination",
        clickable: true,
      },
      breakpoints: {
        1500: {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        1200: {
            slidesPerView: 3.1,
            spaceBetween: 20,
        },
        800: {
          slidesPerView: 2.1,
          spaceBetween: 10,
        },
      },
    });
});

